export const FlowModelTypeTag = "flow";
export type FlowModelType = typeof FlowModelTypeTag;
export const RestServiceModelTypeTag = "restService";
export type RestServiceModelType = typeof RestServiceModelTypeTag;
export const DataTypeModelTypeTag = "dataType"; // object gitti -> √∂nce Type -> sonra dataType oldu, bu sorun oluyorsa beni arayƒ±n, hatta beni aramayƒ±n ezgi bana object kullanƒ±lmƒ±yor dedi, yap dedi, monorepo k√∂t√º dedi, ezgi dedi, ezgi'yi arayƒ±n üòÅ
export type DataTypeModelType = typeof DataTypeModelTypeTag;
export const SoapServiceModelTypeTag = "soapService";
export type SoapServiceModelType = typeof SoapServiceModelTypeTag;
export const ConstantModelTypeTag = "constant";
export type ConstantModelType = typeof ConstantModelTypeTag;
export type EnumModelType = "enum";

export type ObjectID = string; // GUID
export type AuthenticationTypes = ["QCLOUDAUTH", "ADFS"];
export type ModelHistoryType = "update" | "delete" | "checkin" | "publish" | "ownerItemDelete";
export type ApplicationObjectType = "application";
export type ModuleObjectType = "module";
export type ModelObjectType = "model"
export type AppSettingsObjectType = "appSettings";
export type QJsonType = "qjson" | "template" | "pageTemplate" | "quickComponent";
export type ModelResponseType = "info" | "body" | "full";
export type ContentType = "qjson" | "js" | "ts" | "yaml" | "css" | "png" | "jpg" | "jpeg" | "svg" | "gif" | "lottie" | "woff" | "woff2" | "ttf" | "otf" | "json";
export type ModelType = ContentType | "bpmn" | "process" | "entityDesigner" | "namedComponent" | FlowModelType | WorkflowModelType | RestServiceModelType | SoapServiceModelType | "step" | /*"flowEntity" //FLOW ENTITY IS GOING BYE BYE (perhaps ü§î) |*/ "appSettings" | DataTypeModelType | ConstantModelType | EnumModelType | "erronous";
export type UsageType = AppSettingsObjectType;
export type ModuleType = "basic";

interface ICloudObject {
    ID: ObjectID;
    name: string;
    ownerUser: ObjectID;
    createDate: Date;
    createdBy: ObjectID;
    updateDate?: Date;
    updatedBy?: ObjectID;
    storeInfo?: IStoreInfo;
    isPermanent?: boolean;
    priority?: number;
}

export interface IStoreInfo {
    storeID: ObjectID;
    useDate: Date;
}

export interface IApplicationDeployParameters {
    gitGroupId: number;
}

export interface IApplication extends ICloudObject {
    objectType: ApplicationObjectType;
    logo?: string;
    color?: string;
    description?: string;
    deployParameters?: IApplicationDeployParameters;
}

export interface IModuleDeployParameters {
    gitServiceRepoId: number;
    gitProductRepoId: number;
}

export interface IModule extends ICloudObject {
    objectType: ModuleObjectType;
    ownerProjectsHint: Array<ObjectID>;
    deployParameters?: IModuleDeployParameters;
    moduleType?: ModuleType;
}

export interface IModelCheckout {
    user: string;
    date: Date;
}

export interface IQJsonAdditionals {
    qjsonType: QJsonType;
    image?: string;
}

export interface IProcessManagerAdditionals {
    token?: string;
    configEndpoint?: string;
}

export interface IEntityDesignerAddtionals {
    token?: string;
    configEndpoint?: string;
}

export interface IModelInfo extends ICloudObject {
    ownerItem: ObjectID;
    ownerType: ApplicationObjectType | ModuleObjectType;
    modelType: ModelType;
    modelAdditionals: IQJsonAdditionals | IProcessManagerAdditionals | IEntityDesignerAddtionals;
    checkouts?: Array<IModelCheckout>;
    usageType?: UsageType;
}

